export default {
    props: ['dataDetail'],
    data() {
        return {

        }
    },
    methods: {
        async ready() {
                await this.$axios.$put('/order/ready', {
                    "id": this.dataDetail.data.id,
                })
                .then((response) => {
                    console.log(response);
                    this.$router.push('/admin/orders')
                }, (error) => {
                    console.log(error);
                    //this.res = error.response.data.message
                });
        },
    },
}
